import React from "react";

class HomeComponent extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      categories: [],
      errMessage: "Oups, somthing went ronge ...",
      waitingMessage: "Loading ...",
      Loading: true,
      error: false,
    };
  }

  // calling the db of category
  componentDidMount = () => {
    fetch("/read/allcategories")
      .then((response) => {
        if (response.ok) return response.json();
        else {
          if (this.state.categories === null) {
            // setState is for show the following message (Oops, something went wrong ...) if there is any error
            this.setState({ error: true });
          }
        }
      })
      .then((data) => {
        console.log(data);
        if (this.state.categories !== null) {
          // We put timer in order to delay the appearance of the data and show at the same time the word (Loading...)
        }
        this.setState({ categories: data, Loading: false });
      })
      .catch((err) => console.log(err));
  };

  redirect = (data) => this.props.history.push("/" + data.name_category);

  render = () => {
    // creating buttons for categories button
    console.log(this.state);
    return (
      <div className="categories ml-5">
        {this.state.Loading ? this.state.waitingMessage : ""}
        {this.state.error ? this.state.errMessage : ""}
        {this.state.categories.map((data, i) => {
          return (
            <div
              className="insertImg ml-4 pt-2 mt-5"
              onClick={() => this.redirect(data)}
              key={i}
            >
              <h4 className="text-uppercase  my-5 pt-4">
                {data.name_category}
              </h4>
              <div className="text-capitalize popular-text">
                Popular {data.name_category}
              </div>
            </div>
          );
        })}
      </div>
    );
  };
}

export default HomeComponent;
