import React from "react";

class MoviesComponent extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      allMovies: [],
      height: "300",
    };
  }

  redirectToHomesPage = () => {
    this.props.history.push("/");
  };
  redirectToSeriesPage = () => {
    this.props.history.push("/series");
  };

  componentDidMount = () => {
    fetch("/read/allmovies")
      // return response.json(); ... before
      // after changing
      .then((response) => {
        if (response.ok) return response.json();
        else {
          if (this.state.allMovies === null) {
            // setState is for show the following message (Oops, something went wrong ...) if there is any error
            this.setState({ errMessage: "Oops, something went wrong ..." });
          }
        }
      })
      .then((data) => {
        console.log(data);
        if (this.state.allMovies !== null) {
          // We put timer in order to delay the appearance of the data and show at the same time the word (Loading...)

          setTimeout(() => {
            this.setState({ waitingMessage: "Loading ..." });
          }, 100);
        }
        this.setState({ allMovies: data });
      })
      .catch((err) => console.log(err));
  };

  render = () => {
    let movies = this.state.allMovies.map((data, i) => {
      return (
        <div className="showmovies mx-0 mb-4" key={i}>
          <img src={data.url_image} alt="" width="150" />
          <h6>{data.title}</h6>
        </div>
      );
    });
    return (
      <div className="mx-auto mt-5 mb-5">
        <div className="movielist mx-5 d-flex flex-wrap justify-content-around">
          {movies}
        </div>
        <div className="buttons">
          <button
            type="button"
            className="btn btn-primary mx-2"
            onClick={this.redirectToHomesPage}
          >
            Home
          </button>
          <button
            type="button"
            className="btn btn-primary mx-2"
            onClick={this.redirectToSeriesPage}
          >
            Series
          </button>
        </div>
        <div>
          <h1 className="margin text-white h1 my-3 py-5">Margin</h1>
        </div>
      </div>
    );
  };
}

export default MoviesComponent;
